.Nav ul {
	list-style-type: none;
	margin: 0;
	padding: 0;
	height: 30px;
	background-color: black;
}

.Nav a {
	text-decoration: none;
	color: white;
}

.Nav a:hover {
	color: grey;
}

.Nav .NavItem {
	display: inline;
	margin: 10px;
	float: right;
}

.Nav .title {
	float: left;
}



